import React from "react";
import blogService from "../services/blogs";
// import PropTypes from "prop-types";
import { Redirect } from "react-router-dom";

class Blog extends React.Component {
  // static propTypes = {
  //   blog: PropTypes.object.isRequired,
  //   user: PropTypes.object.isRequired,
  //   destroy: PropTypes.func.isRequired
  // };
  constructor(props) {
    super(props);
    this.state = {
      blog: this.props.blog,
      newComment: ""
    };
    this.setNotification = props.setNotification;
  }
  
  handleLike = async () => {
    const blogObject = {
      title: this.state.blog.title,
      author: this.state.blog.author,
      url: this.state.blog.url,
      likes: this.state.blog.likes + 1,
      comments: this.state.blog.comments
    };
    const likedBlog = await blogService.update(this.state.blog._id, blogObject);
    this.setState({
      blog: likedBlog
    });
  };

  deleteBlog = async () => {
    if (
      window.confirm(
        `delete ${this.state.blog.title} by ${this.state.blog.author}?`
      )
    ) {
      try {
        await blogService.remove(this.state.blog, this.props.user);
        this.props.destroy(this.state.blog);
      } catch (exception) {
        console.log(exception);
      }
    }
  };

  handleCommentChange = event => {
    this.setState({ newComment: event.target.value });
  };

  addComment = async event => {
    event.preventDefault();

    const comment = {
      content: this.state.newComment,
      blog: this.state.blog
    };

    const response = await blogService.comment(this.state.blog._id, comment);
    const updatedBlog = {
      _id: this.state.blog._id,
      title: this.state.blog.title,
      author: this.state.blog.author,
      url: this.state.blog.url,
      likes: this.state.blog.likes,
      comments: this.state.blog.comments.concat(response)
    };
    this.setState({
      blog: updatedBlog,
      newComment: ""
    });
    this.setNotification(
      `comment '${comment.content}' added to blog '${this.state.blog.title}'`
    );
  };

  render() {
    try {
      const addedBy =
        this.props.blog.user === null
          ? "unknown"
          : this.props.blog.user.name === undefined
            ? this.props.blog.user.username
            : this.props.blog.user.name;
      const deleteButton =
        this.props.blog.user.username === this.props.user.username ? (
          <button onClick={this.deleteBlog}>delete</button>
        ) : this.props.blog.user === null ? (
          <button onClick={this.deleteBlog}>delete</button>
        ) : (
          <br />
        );
      return (
        <div>
          {this.state.blog === undefined ? (
            <div>
              <Redirect to="/" />
            </div>
          ) : (
            <div>
              <h1>
                {this.state.blog.title} by {this.state.blog.author}
              </h1>
              <a href={this.state.blog.url}>{this.state.blog.url}</a>
              <p>
                {this.state.blog.likes} likes{" "}
                <button onClick={this.handleLike}>like</button>
              </p>
              <p>added by {addedBy}</p>
              {deleteButton}
              <h2>Comments</h2>
              {this.state.blog.comments.map(comment => (
                <li key={comment._id}>{comment.content}</li>
              ))}
              <form onSubmit={this.addComment}>
                <input
                  type="text"
                  name="content"
                  value={this.state.newComment}
                  placeholder="Add comment.."
                  onChange={this.handleCommentChange}
                />
                <button type="submit">add comment</button>
              </form>
            </div>
          )}
        </div>
      );
    } catch (exception) {
      return (
        <div>
          <Redirect to="/" />
        </div>
      );
    }
  }
}

export default Blog;



