import React from "react";
import blogService from "./services/blogs";
import Notification from "./components/Notification";
import LoginForm from "./components/LoginForm";
import loginService from "./services/login";
import BlogForm from "./components/BlogForm";
import Togglable from "./components/Togglable";
import { BrowserRouter as Router, Route, NavLink } from "react-router-dom";
import Blogs from "./components/Blogs";
import Users from "./components/Users";
import userService from "./services/user";
import User from './components/User'
import Blog from './components/Blog'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      blogs: [],
      user: null,
      username: "",
      password: "",
      notification: null,
      error: false,
      blogFormVisible: null,
      users: []
    };
    this.deleteBlogFromBlogs = this.deleteBlogFromBlogs.bind(this);
  }

  deleteBlogFromBlogs = (props) => {
    this.setState({
      blogs: this.state.blogs.filter(blog => blog._id !== props._id),
      notification: `${props.title} by ${props.author} deleted`
    });
    setTimeout(() => {
      this.setState({ notification: null, error: false });
    }, 3000);
  };

  async componentDidMount() {
    const blogs = await blogService.getAll();
    blogs.sort(function(a, b) {
      return b.likes - a.likes;
    });
    const users = await userService.getAll();

    this.setState({ blogs, users });
    const loggedUserJSON = window.localStorage.getItem("loggedBlogappUser");
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      this.setState({ user });
      blogService.setToken(user.token);
    }
  }

  handleLoginFieldChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  login = async (event) => {
    event.preventDefault();
    try {
      const user = await loginService.login({
        username: this.state.username,
        password: this.state.password
      });

      window.localStorage.setItem("loggedBlogappUser", JSON.stringify(user));
      blogService.setToken(user.token);
      this.setState({ username: "", password: "", user });
    } catch (exception) {
      this.setState({
        notification: "Invalid username or password",
        error: true,
        password: ""
      });
      setTimeout(() => {
        this.setState({ notification: null, error: false });
      }, 3000);
    }
  };

  logout = async (event) => {
    event.preventDefault();
    window.localStorage.removeItem("loggedBlogappUser");
    this.setState({
      user: null,
      notification: "Logged out"
    });
    setTimeout(() => {
      this.setState({ notification: null });
    }, 3000);
  };

  toggleBlogForm = () => {
    this.state.blogFormVisible === null
      ? this.setState({ blogFormVisible: true })
      : this.setState({ blogFormVisible: null });
  };

  newBlog = async (props) => {
    const blogs = await blogService.getAll();
    blogs.sort(function(a, b) {
      return b.likes - a.likes;
    });

    this.setState({
      blogs,
      notification: `A new blog '${props.title}' by ${props.author} added`
    });
    setTimeout(() => {
      this.setState({ notification: null });
    }, 3000);
  };

  userById = (id) => {
    return this.state.users.find(user => user.id === id)
  }

  blogById = (id) => {
    return this.state.blogs.find(blog => blog._id === id)
  }

  setNotification = (message) => {
    this.setState({
      notification: message
    })
    setTimeout(() => {
      this.setState({
        notification: null
      })
    }, 3000)
  }

  render() {
    const loginForm = () => (
      <LoginForm
        visible={this.state.visible}
        username={this.state.username}
        password={this.state.password}
        handleChange={this.handleLoginFieldChange}
        handleSubmit={this.login}
      />
    );

    return (
      <div className="content">
        <Notification
          message={this.state.notification}
          error={this.state.error}
        />
        <Router>
          <div>
            <NavLink to="/">blogs</NavLink> &nbsp;
            <NavLink to="/users">users</NavLink> &nbsp;
            {this.state.user === null ? (
              loginForm()
            ) : (
              <div>
                <h2>Blog App</h2>
                {this.state.user.name === undefined ? (
                    <p>
                      {this.state.user.username} logged in{" "}
                      <button onClick={this.logout}>Logout</button>
                    </p>
                ) : (
                    <p>
                      {this.state.user.name} logged in{" "}
                      <button onClick={this.logout}>Logout</button>
                    </p>
                )}
                <Togglable buttonLabel="Create new">
                  <BlogForm newBlog={this.newBlog} />
                </Togglable>
                <br />
                <Route
                  exact
                  path="/"
                  render={() => (
                    <Blogs
                      blogs={this.state.blogs}
                      user={this.state.user}
                      deleteBlogFromBlogs={this.deleteBlogFromBlogs.bind(this)}
                    />
                  )}
                />
                <Route
                  exact
                  path="/users"
                  render={() => <Users users={this.state.users} />}
                />
                <Route exact path="/users/:id" render={({match}) =>
                <User user={this.userById(match.params.id)} />} />
                <Route exact path="/blogs/:id" render={({match}) =>
                <Blog blog={this.blogById(match.params.id)} destroy={this.deleteBlogFromBlogs.bind(this)} user={this.state.user} setNotification={this.setNotification.bind(this)}/>} />
              </div>
            )}
          </div>
        </Router>
      </div>
    );
  }
}

export default App;



