import React from "react";
import { NavLink } from 'react-router-dom'

const Users = ({ users }) => {
  const blogsAddedStyle = {
    fontWeight: "bold"
  };
  return (
    <div>
      <h2>Users</h2>
      <table>
        <tbody>
          <tr>
            <td />
            <td style={blogsAddedStyle}>blogs added</td>
          </tr>
          {users.map(user => (
            <tr key={user.id}>
              <td>
                <NavLink to={`/users/${user.id}`}>
                  {user.name !== undefined ? (
                    <div>{user.name}</div>
                  ) : (
                    <div>{user.username}</div>
                  )}
                </NavLink>
              </td>
              <td>{user.blogs.length}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Users;



