import React from "react";

const User = ({ user }) => {
  return (
    <div>
      {user.name !== undefined ? (
        <h1>{user.name}</h1>
      ) : (
        <h1>{user.username}</h1>
      )}
      <h2>Added blogs</h2>
      {user.blogs.map(blog => <li key={blog._id}>{blog.title} by {blog.author}</li>)}
    </div>
  );
};

export default User;



