import React from 'react'
import { NavLink } from 'react-router-dom'


const Blogs = ({ blogs, user, deleteBlogFromBlogs }) => {
    const blogStyle = {
        paddingTop: 10,
        paddingLeft: 2,
        border: 'solid',
        borderWidth: 1,
        marginBottom: 5,
        listStyleType: 'none'
      }
    return (
        <div>
        {blogs.map(blog => (
            <li key={blog._id} style={blogStyle}>
            <NavLink to={`/blogs/${blog._id}`}>
                {blog.title} by {blog.author}
            </NavLink>
            </li>
          ))}
        </div>
    )
}

export default Blogs


